.header {
height : 60px;
display: flex;
align-items:center;
background-color: #131921;
position: sticky;
top: 0;
z-index: 100;
}

.header_logo {
    width: 100px;
    object-fit:contain;
    margin:0 20px;
    margin-top:18px;
}

.header_search{
    display: flex;
    flex: 1;
    align-items: center;
    border-radius: 24px;
    /* cursor:pointer; */
}

.header_searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}

.header_searchIcon{
    padding: 5px;
    height: 22px;
    background-color: #cd9042;
}

.header_nav{
    display:flex;
    justify-content: space-evenly;
}
.header_option{
    display : flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
    color:whitesmoke;
}
.header_optionBasket{
    display: flex;
    align-items: center;
    color: whitesmoke;
}
.header_basketCount{
margin-left : 10px;
margin-right: 10px;
}
.header_optionLineOne{
    font-size: 16px;
    font-weight: 500;
}
.header_optionLineTwo{
    font-size: 16px;
    font-weight: 800;
}